@media(max-width:768px){.hideOnMobile{display:none}}@media(max-width:1024px){.hideOnTablet{display:none}}@media(max-width:768px){.hideOnTablet{display:inherit}}.ItemStateDropdown__toggle{display:flex;align-items:center;height:100%;width:100%;margin-bottom:0;cursor:pointer;font-size:.875rem;color:#374151;border-width:1px;border-radius:.25rem;border-color:transparent}.ItemStateDropdown__toggle:hover{color:#161e2e}.ItemStateDropdown__menu{overflow:hidden;width:340px;max-width:340px;padding:0;position:relative}.ItemStateDropdown__menu.left{right:0;left:auto}.ItemStateDropdown__menu.right{right:auto;left:0}.ItemStateDropdown__stateListPanel{text-align:left;padding:0}.ItemStateDropdown__stateListPanel.v-enter-active,.ItemStateDropdown__stateListPanel.v-leave-active{transition:all .15s linear}.ItemStateDropdown__stateListPanel.v-enter,.ItemStateDropdown__stateListPanel.v-leave-to{position:absolute;transform:translateX(-99%)}.ItemStateDropdown__stateListPanel__state{cursor:pointer;padding:.5rem;color:#6b7280;border-radius:.25rem;border-width:1px;border-color:#f4f5f7;margin-bottom:.25rem}.ItemStateDropdown__stateListPanel__state.active,.ItemStateDropdown__stateListPanel__state:hover{--bg-opacity:1;background-color:#f4f5f7;background-color:rgba(244,245,247,var(--bg-opacity));color:#161e2e;border-color:#e5e7eb}.ItemStateDropdown__stateListPanel__state .ItemState__light{margin-right:15px}.ItemStateDropdown__stateSavePanel{height:100%;display:flex;flex-direction:column;justify-content:space-evenly;overflow-y:auto;overflow-x:hidden}.ItemStateDropdown__stateSavePanel.v-enter-active,.ItemStateDropdown__stateSavePanel.v-leave-active{transition:all .15s linear}.ItemStateDropdown__stateSavePanel.v-enter,.ItemStateDropdown__stateSavePanel.v-leave-to{position:absolute;transform:translateX(100%)}.ItemStateDropdown__stateSavePanel .ItemState__light{margin-left:5px}.ItemStateDropdown__saveButton{line-height:1.375!important;height:auto!important;padding-top:.5rem!important;padding-bottom:.5