.ItemState{display:flex;align-items:center;min-width:0}.ItemState__light{flex-shrink:0;border-color:#f4f5f7;margin-left:.5rem;margin-right:.5rem;background:#f1f3f4}.ItemState__light,.ItemState__light.muted{border-radius:9999px;height:.75rem;width:.75rem;border-width:1px}.ItemState__light.muted{content:"";position:relative;display:flex;align-items:center;justify-content:center;border-color:#9fa6b2;--bg-opacity:1;background-color:#fff;background-color:rgba(255,255,255,var(--bg-opacity))}.ItemState__light.hideStateBullet{display:none}