@media(max-width:768px){.hideOnMobile{display:none}}@media(max-width:1024px){.hideOnTablet{display:none}}@media(max-width:768px){.hideOnTablet{display:inherit}}.AssetPreview.list{width:80px;height:50px;overflow-y:hidden;overflow-x:hidden;background-color:#f1f3f4;border-radius:3px;margin-right:1em;flex-shrink:0;flex-grow:1;display:flex;justify-content:center;align-items:center;transition:height .2s,width .3s}.AssetPreview.list svg{width:30px;height:40px}.AssetPreview.list img{width:80px;height:auto}@media(min-width:641px)and (max-width:768px){.AssetPreview.list{width:100%;height:200px}}.AssetPreview.detail{width:100%;margin-right:0;align-items:flex-start;display:flex;justify-content:center}.AssetPreview.detail img{max-width:100%;max-height:100%}.AssetPreview.detail iframe{height:80vh}.AssetElement.GridView .AssetPreview{width:100%;height:200px;max-height:200px;background-color:#f1f3f4;overflow-y:hidden;display:flex;justify-content:center;align-items:flex-start;margin-right:0}.AssetElement.GridView .AssetPreview img{width:inherit}.AssetPreview__assemblyExecuting{background-color:#7e5bef!important;color:#fefefe;font-size:.8em;font-weight:700;padding:0 1em;border-radius:3px}.AssetPreview__video{display:flex;justify-content:center}