.BulkBar{justify-content:center;width:100%}.BulkBar,.BulkBar__checkbox{display:flex;align-items:center}.BulkBar__checkbox{align-content:center;align-self:stretch;border-width:1px;border-radius:.25rem;border-color:transparent;cursor:pointer;margin-right:.25rem;padding:.5rem;margin-left:1px}.BulkBar__checkbox:hover{--bg-opacity:1;background-color:#f4f5f7;background-color:rgba(244,245,247,var(--bg-opacity));border-color:#e5e7eb}