.CommentBox__mentionsDropdown{position:absolute;z-index:110;height:auto;max-height:400px;width:300px;overflow:auto;border-radius:4px;border:1px solid #e0e0e0}.CommentBox__mentionsContent{display:flex;flex-direction:column;flex-grow:1;--bg-opacity:1;background-color:#fff;background-color:rgba(255,255,255,var(--bg-opacity));width:100%;padding:1rem}.CommentBox__mentionsResult{display:flex;align-items:center;padding:.25rem;justify-content:space-between;cursor:pointer}.CommentBox__mentionsResult b{--bg-opacity:1;background-color:#faca15;background-color:rgba(250,202,21,var(--bg-opacity));color:#1c64f2;font-weight:400}.CommentBox__mentionsResult.highlighted,.CommentBox__mentionsResult:hover{background-color:#f1f3f4;border-radius:4px;color:inherit}.CommentBox__mentionsResult.inactive{color:#cbd3d8}.CommentBox__mentionsResult.confirm{background-color:#ffce4b}.CommentBox__mentionCategory{font-size:.75rem;color:#4b5563;padding-left:.5rem;padding-right:.5rem;border-radius:.5rem}.CommentBox__textarea textarea{overflow:hidden}