.dropzone{display:flex;flex-direction:column;align-items:center;width:100%;min-height:100%;padding:1rem}.dropzone .dz-message{margin:0;padding:0;cursor:pointer}.dropzone .dz-upload-button{font-size:1.125rem;text-decoration:underline;color:#5850ec}.dropzone .dropzone-previews{width:100%;display:contents}.dropzone-bordered{padding:1.5rem;border:2px dashed #d2d6dc;border-radius:.375rem;cursor:pointer}.dropzone-bordered:hover{--bg-opacity:1;background-color:#f0f5ff;background-color:rgba(240,245,255,var(--bg-opacity));border-color:#9fa6b2}