.RichTextTooltip{box-sizing:border-box;-moz-box-sizing:border-box;border-radius:5px;padding:3px 7px;margin:0;background:#607d8b;border:1px solid #607d8b;color:#9e9e9e}.RichTextTooltip[x-placement^=top]>.popper__arrow{border-color:#607d8b transparent transparent}.RichTextTooltip[x-placement^=bottom]>.popper__arrow{border-color:transparent transparent #607d8b}.RichTextTooltip[x-placement^=right]>.popper__arrow{border-color:transparent #607d8b transparent transparent}.RichTextTooltip[x-placement^=left]>.popper__arrow{border-color:transparent transparent transparent #607d8b}.RichTextTooltip[x-out-of-boundaries]{opacity:0}.RichTextTooltip input::-webkit-input-placeholder,.RichTextTooltip textarea::-webkit-input-placeholder{color:#757575}.RichTextTooltip input::-moz-placeholder,.RichTextTooltip textarea::-moz-placeholder{color:#757575}.RichTextTooltip input:-ms-input-placeholder,.RichTextTooltip textarea:-ms-input-placeholder{color:#757575}.RichTextTooltip input::-ms-input-placeholder,.RichTextTooltip textarea::-ms-input-placeholder{color:#757575}.RichTextTooltip input::placeholder,.RichTextTooltip textarea::placeholder{color:#757575}.RichTextTooltip a{color:#90caf9}.RichTextTooltip.opened{transition:transform .25s ease-out,opacity .2s}.RichTextTooltip input[type=text],.RichTextTooltip textarea{background:#eee;border:none;outline:none}.RichTextTooltip input[type=text]{padding:0 4px}